import React, { Component } from 'react';
import { NavBar, Icon, } from 'antd-mobile';
import Content from '../../views/common/Content';

import '../../assets/css/traffic.css';

export default class TrafficBusScreen extends Component
{
    render()
    {
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => this.props.history.goBack()}>
                    客运
                </NavBar>
                <Content style={{backgroundColor: '#efeff4'}}>
                    <div className="trafficDetailCard">
                        <img src={require('../../assets/images/traffic/bus_1.jpg')} alt='' className="trafficDetailImage"/>
                        
                        <div className="trafficDetailNameRow">
                            <div className="trafficDetailNames">
                                <div className="trafficDetailName">
                                    新县汽车站
                                </div>
                                <div className="trafficDetailSubName">
                                    Xinxian County bus station
                                </div>
                            </div>
                        </div>

                        <div className="trafficDetailLR">
                            <div className="trafficDetailLRItem">
                                <div className="trafficDetailLRSubItem">联系电话</div>
                                <div className="trafficDetailLRSubItem">0376-2987163</div>
                            </div>
                            <div className="trafficDetailSplitLR"/>
                            <div className="trafficDetailLRItem">
                                <div className="trafficDetailLRSubItem">地址</div>
                                <div className="trafficDetailLRSubItem">河南省新县新集镇</div>
                            </div>
                        </div>

                        <div className="trafficDetailIntro">
                            <div className="trafficDetailIntroLine"/>
                            <div className="trafficDetailIntroText">新县汽车站简介</div>
                            <div className="trafficDetailIntroLine"/>
                        </div>

                        <div className="trafficDetailContent">
                            <div className="trafficDetailLongText">
                                新县汽车站，地处河南，是国家城市枢纽站之一。始建于1953年3月，2005年8月改制为新县市新运站务有限责任公司新县汽车站。营运线路上百条，班车通达京、津、沪、粤、鄂、皖、浙、闽等16个省市中多个大中城市，日运送旅客千余人次，日发班次几百余个。车站周边商家众多，形成了为旅客提供吃、住、行、游、乐一条龙的服务产业链。
                            </div>

                            <div className="trafficDetailTitle1">客运发车表</div>
                            <table className="trafficDetailTable" border="1px">
                                <tbody>
                                    <tr>
                                        <th style={{width: '30%'}}>终点站</th>
                                        <th>发车时间</th>
                                    </tr>
                                    <tr>
                                        <td>武汉</td>
                                        <td>6:00 7:00 8:00 8:40 9:20 11:00 13:20 15:00</td>
                                    </tr>
                                    <tr>
                                        <td>郑州</td>
                                        <td>7:00 7:40 14:00</td>
                                    </tr>
                                    <tr>
                                        <td>北京</td>
                                        <td>7:00</td>
                                    </tr>
                                    <tr>
                                        <td>大连</td>
                                        <td>7:10</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </Content>
            </div>
        );
    }
}


